import React from "react";
import styled from 'styled-components';
import { slide as Menu } from 'react-burger-menu';
import { Link } from "react-router-dom";

function NavBar() {
    const styles = {
      bmBurgerButton: {
        position: 'fixed',
        width: '36px',
        height: '30px',
        left: '36px',
        top: '36px'
      },
      bmBurgerBars: {
        background: '#2d303c'
      },
      bmBurgerBarsHover: {
        background: '#a90000'
      },
      bmCrossButton: {
        height: '24px',
        width: '24px'
      },
      bmCross: {
        background: '#bdc3c7'
      },
      bmMenuWrap: {
        position: 'fixed',
        height: '100%'
      },
      bmMenu: {
        background: '#373a47',
        padding: '2.5em 1.5em 0',
        fontSize: '1.15em'
      },
      bmMorphShape: {
        fill: '#373a47'
      },
      bmItemList: {
        color: '#b8b7ad',
        padding: '0.8em'
      },
      bmItem: {
        display: 'flex'
      },
      bmOverlay: {
        background: 'rgba(0, 0, 0, 0.3)'
      }
    };

    return (
        <Menu animation={'stack'} styles={styles}>
            <MenuContainer>
                <StyledLink to="/">Home</StyledLink>
                <StyledLink to="/about">About</StyledLink>
            </MenuContainer>
        </Menu>
    );
}

const MenuContainer = styled.div`
    display: flex;
    flex-direction: column;
`;

const StyledLink = styled(Link)`
    color: #f7f7f7;
`;

export { NavBar };